    animation-name: showFromRotate;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
    transform-origin: 0 0;
  }

  .fromTop {
    animation-name: fromTop;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }

  .fromBottom {
    animation-name: fromBottom;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }


  .fadeIn {
    animation-name: fadeIn;
    animation-duration: 0.2s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }

  .fadeOut {
    animation-name: fadeOut;
    animation-duration: 0.2s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }

  .fromScale {
    animation-name: fromScale;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }

  .fromLeft {
    animation-name: fromLeft;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: ease-in;
  }

  .fromDeepRight {
    animation-name: fromDeepRight;
    animation-duration: 0.6s;
    animation-fill-mode: both;
    animation-timing-function: ease-in;
  }

  .fromRight {
    animation-name: fromRight;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    animation-timing-function: ease-in;
  }

  .wholeFromBottom {
    animation-name: wholeFromBottom;
    animation-duration: 0.6s;
    animation-fill-mode: both;
    animation-timing-function: ease-in;
  }

  .showFromRotate {
    animation-name: showFromRotate;
    animation-duration: 0.5s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
    transform-origin: 0 0;
  }

  .svgdashed {
    stroke-dasharray: 1700;
    stroke-dashoffset: 1700;
    animation-name: svgdashed;
    animation-duration: 3s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
    animation-delay: 1s;
  }

  .svgdashed-arrow {
    stroke-dasharray: 400;
    stroke-dashoffset: -400;
    animation-name: svgdashed;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
  }

  .svgdashed-anketa {
    stroke-dasharray: 600;
    stroke-dashoffset: -600;
    animation-name: svgdashed;
    animation-duration: 2.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
  }

  .svgdashed-footer {
    stroke-dasharray: 700;
    stroke-dashoffset: 700;
    animation-name: svgdashed;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
  }

  .slidertudasuda {
    animation-name: slidertudasuda;
    animation-duration: 1.5s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
  }

  .animate-open-1 {
    animation-name: animate-open;
    animation-duration: 1s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;
    animation-delay: 0.3s;

  }

  .animate-open-2 {
    animation-name: animate-open;
    animation-duration: 1s;
    animation-fill-mode: both;
    animation-timing-function: ease-out;

  }

  @keyframes animate-open {
    0% {
      max-height: 30px;
    }

    80% {
      max-height: 80px;
    }

    100% {
      max-height: 30px;
    }
  }

  @keyframes slidertudasuda {
    0% {
      transform: translateX(0)
    }

    50% {
      transform: translateX(-10%)
    }

    100% {
      transform: translateX(0)
    }
  }

  @keyframes svgdashed {
    to {
      stroke-dashoffset: 0;
    }
  }

  @keyframes fromScale {
    from {
      opacity: 0;
      transform: scale(0.3);
    }

    to {
      opacity: 1;
      transform: scale(1);
    }
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes fadeOut {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }

  @keyframes fromTop {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes fromBottom {
    from {
      opacity: 0;
      transform: translateY(3rem);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }


  @keyframes fromLeft {
    from {
      opacity: 0;
      transform: translateX(-40%);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes showFromRotate {
    from {
      opacity: 0;
      transform: rotate(-20deg) scale(0.8);
    }

    to {
      opacity: 1;
      transform: rotate(0) scale(1);
    }
  }

  @keyframes fromRight {
    from {
      opacity: 0;
      transform: translateX(40%);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  @keyframes fromDeepRight {
    from {
      opacity: 0;
      transform: translateX(80%);
    }

    to {
      opacity: 1;
      transform: translateX(0);
    }
  }


  @keyframes wholeFromBottom {
    from {
      opacity: 0;
      transform: translateY(100%);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }


  @keyframes map-pulse {
    0% {
      opacity: 1;
      transform: scale(1);
    }

    100% {
      opacity: 0;
      transform: scale(2);
    }
  }


