.art-nav {


  &__list {
    list-style: none;
    padding: 0;
  }

  &__item {
    margin: 0 0 3.2rem;

    @media (max-width: $tablet) {
      margin: 0 0 1rem;
    }

    @media (max-width: $mobile) {
      margin: 0 0 1.6rem;
    }
  }

  &__link {
    color: $black;
    border-bottom: 1px solid currentColor;
    font-size: 2.5rem;
    line-height: 1.15;

    &.active {
      color: $purple;
    }

    @media (max-width: $tablet) {
      font-size: 2rem;
    }

    @media (max-width: $mobile) {
      font-size: 1.4rem;
    }
  }
}
