.container {
  max-width: 152rem;
  margin: 0 auto;
  padding: 0 3rem;
  box-sizing: content-box;
  position: relative;

  @media (max-width: $laptop) {
    min-width: 67.8rem;
    padding: 0 4.5rem;
  }

  @media (max-width: $tablet) {
    min-width: initial;
    max-width: initial;
    padding: 0 20px;
  }
}

